import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue'),
  },
  {
    path: '/counsel',
    name: 'Counsel',
    component: () => import('../views/CounselTrouble.vue'),
  },
  {
    path: '/qna',
    name: 'QnA',
    component: () => import('../views/QnA.vue'),
  },
  {
    path: '/vision',
    name: 'Vision',
    component: () => import('../views/Vision.vue'),
  },
  {
    path: '/introduce',
    name: 'Introduce',
    component: () => import('../views/Introduce.vue'),
  },
  {
    path: '/allies',
    name: 'Allies',
    component: () => import('../views/Allies.vue'),
  },
  {
    path: '/affirming',
    name: 'AffirmingChurch',
    component: () => import('../views/AffirmingChurch.vue'),
  },
  {
    path: '/great_name',
    name: 'GreatName',
    component: () => import('../views/GreatName.vue'),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
